<template>
  <ItemDetails
    :value="value"
    :history="history"
    :overview="overview"
    @edit="handleEdit"
    @input="handleInput"
    @delete="handleDelete"
  >
    <template #title>Workspace Details</template>
  </ItemDetails>
</template>

<script>
import { workspace } from "@/api/factory.js";
import ItemDetails from "@/components/common/display/item-details/ItemDetails.vue";

export default {
  name: "WorkspaceDetails",

  components: { ItemDetails },

  props: {
    value: {
      type: Boolean,
      default: false,
    },

    columns: {
      type: Array,
      required: true,
    },

    workspace: {
      type: Object,
      required: true,
    },
  },

  data() {
    return {
      isLoading: false,
      history: [],
    };
  },

  computed: {
    overview() {
      return this.columns.map((column) => ({
        id: column.id,
        label: column.label,
        value: this.workspace[column.name],
      }));
    },
  },

  watch: {
    workspace: {
      immediate: true,
      deep: true,
      handler() {
        if (this.workspace.id) {
          this.getWorkspaceHistory();
        }
      },
    },
  },

  methods: {
    handleInput(value) {
      this.$emit("input", value);
    },

    handleEdit() {
      this.$emit("input", false);
      this.$emit("edit", this.workspace.id);
    },

    handleDelete() {
      this.$emit("input", false);
      this.$emit("delete", this.workspace.id);
    },

    /* api functions */

    async getWorkspaceHistory() {
      this.isLoading = true;

      const { error, payload } = await workspace.getWorkspaceHistory(
        this.workspace.id
      );

      this.isLoading = false;

      if (error) {
        this.$alert.error(error);
        return;
      }

      this.history = payload;
    },

    /* ... */
  },
};
</script>

<style lang="scss" scoped></style>
